.loader-image{
  position:fixed;
  top:0;
  left:0;
  width:100%;
  height:100%;
  background:rgba(0,0,0,.6);
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:column;
      flex-direction:column;
  -ms-flex-pack:center;
      justify-content:center;
  -ms-flex-align:center;
      align-items:center;
  opacity:0;
  z-index:-1;
  color:#fff;
}

.loader-image.is-loading{
  opacity:1;
  z-index:20;
}

.icon-loader{
  width:100px;
  -webkit-animation:zoom .9s alternate infinite;
          animation:zoom .9s alternate infinite;
}

@-webkit-keyframes zoom{
  0%{
    -webkit-transform:scale(.80);
            transform:scale(.80);
  }
  100%{
    -webkit-transform:scale(1.2);
            transform:scale(1.2);
  }
}

@keyframes zoom{
  0%{
    -webkit-transform:scale(.80);
            transform:scale(.80);
  }
  100%{
    -webkit-transform:scale(1.2);
            transform:scale(1.2);
  }
}






