.hero-proyect {
  padding-bottom: 30px;
  border-bottom: 0.2px solid rgba(255, 255, 255, 0.9);
  -webkit-box-shadow: 0 0 4px rgba(255, 255, 255, 0.2);
          box-shadow: 0 0 4px rgba(255, 255, 255, 0.2); }

.hero {
  text-align: center;
  color: #fff; }
  @media screen and (min-width: 320px) and (max-width: 1200px) {
    .hero {
      padding: 1em; } }
